import PressEmpty from '../press-empty/press-empty.vue';
import PressList from '../press-list/press-list';
import PressLoading from '../press-loading/press-loading';


export default {
  name: 'PressTeamSearch',
  options: {
    virtualHost: true,
    styleIsolation: 'shared',
  },
  components: {
    PressEmpty,
    PressList,
    PressLoading,
  },
  props: {
    placeholder: {
      type: String,
      default: '',
    },
    customStyle: {
      type: String,
      default: '',
    },
    defaultSearchWords: {
      type: String,
      default: '',
    },
    loading: {
      type: Boolean,
      default: false,
    },
    loadingText: {
      type: String,
      default: '...',
    },
    finished: {
      type: Boolean,
      default: false,
    },
    finishedText: {
      type: String,
      default: '',
    },
    immediateCheck: {
      type: Boolean,
      default: false,
    },
    teamList: {
      type: Array,
      default: () => ([]),
    },
    showContent: {
      type: Boolean,
      default: true,
    },
  },
  emits: [
    'search',
    'blur',
    'focus',
    'clear',
    'clickTeam',
  ],
  data() {
    return {
      inputFocus: false,
      searchWords: '',
    };
  },
  computed: {
    innerLoading: {
      get() {
        return this.loading;
      },
      set(value) {
        this.$emit('update:loading', value);
      },
    },
    showFakePlaceholder() {
      return !this.searchWords && !this.inputFocus;
    },
    innerPlaceholder() {
      return this.showFakePlaceholder ? '' : this.placeholder;
    },
  },
  watch: {
    defaultSearchWords: {
      handler(val) {
        if (val) {
          this.searchWords = val;
        }
      },
      immediate: true,
    },
  },
  mounted() {
  },
  methods: {
    onBlur() {
      this.inputFocus = false;
      this.$emit('blur', this.searchWords);
    },
    onFocus() {
      this.inputFocus = true;
      this.$emit('focus', this.searchWords);
    },
    onKeyPress($event) {
      let evt = $event;
      evt = (evt) || window.event;
      const charCode = (evt.which) ? evt.which : evt.keyCode;
      if (charCode === 13) {
        this.$refs.searchInput.blur();
        this.onSearch();
      }
      return true;
    },
    onSearch() {
      this.$emit('search', this.searchWords);
    },
    clearInput() {
      this.searchWords = '';
      this.inputFocus = true;
      this.$emit('clear');
    },
    clickTeam(team) {
      this.$emit('clickTeam', team);
    },
    load() {
      this.$emit('load');
    },
  },
};

